  font-weight: bold;
}

/* 响应式设计 */
@media (max-width: 768px) {
  .table th, .table td {
    padding: 8px;
    font-size: 14px;
  }
  .table th, .table td {
    white-space: nowrap;
  }
  .table tr {
    display: block;
    margin-bottom: 10px;
  }
  .table tr td {
    display: flex;
    justify-content: space-between;
    padding: 8px;
    border: 1px solid #ddd;
  }
  .table thead {
    display: none;
  }
  .table td::before {
    content: attr(data-label);
    font-weight: bold;
    width: 50%;
    display: inline-block;
  }
}
</style>
